.wholContainer{
  overflow: hidden;
  position: relative;
  width: 100vw;
  min-height: 700px;
  // background-color: beige;

    .mainTitle{
      text-align: center;
      padding: 30px 0px 5px 0px;
      margin-bottom: 0px;
      font-size: 20px;
      font-weight: 600;
    }

    .status {
      text-align: center;
      margin-bottom: 0px;
      font-size: 15px;
      font-weight: 400;
    }

    .order_info_wrap {
      position: relative;
      // background-color: aqua;
      width: 50vw;
      margin: 0px auto;
      margin-bottom: 262px;
  
      @media screen and (max-width:1000px) {
        width: 70vw;
      }
  
      @media screen and (max-width:510px) {
        width: 90vw;
      }
  
      .orderlist_Check_wrap{
        position: relative;
        // background-color: bisque;
        border: 0.5px solid black;
        margin-top: 20px;
  
        .older_date{
          padding: 20px 25px;
          font-size: 15px;
          margin-bottom: 0px;
        }
    
        .older_image_info{
          // background-color: blueviolet;
          padding: 0px 10px;
          display: flex;
      
          .older_image{
            width: 100px;
            height: 100px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            cursor: pointer;
      
            @media screen and (max-width:332px) {
              background-size: contain;;
            }
          }
      
          .pdnameprice{
            font-size: 15px;
            position: relative;
            margin: auto 0px;
            margin-left: 10px;
      
            .pdname {
              font-weight: 600;
              cursor: pointer;
            }
      
            .pdprice{
              font-size: 12px;
            }
            p{
              margin: 0px;
            }
          }
        }
  
        .older_detail_info{
          font-size: 15px;
          margin-left: 30px;
          margin-top: 10px;
        }

        .return_title{
          margin-top: 20px;
          margin-bottom: 0px;
          padding: 0px 30px;
          font-size: 15px;
        }
  
        .return_reason{
          margin-top: 20px;
          margin-bottom: 0px;
          padding: 0px 40px;
  
          .reason_text{
            font-size: 18px;
            font-weight: 700;
          }
        }
  
        .return_etc{
          margin-top: 20px;
          margin-bottom: 0px;
          padding: 0px 40px;
  
          .reason_etc_text{
            font-size: 15px;
          }
        }
  
        .infoCancelContainer {
          position: relative;
          margin: 0px auto;
          // background-color: beige;
          padding: 15px 40px;
          text-align: center;
  
          @media screen and (max-width:1000px) {
          padding: 15px 10px;
          }
  
            .reason_div {
              position: relative;
              margin-bottom: 20px;
              
              .reason_children{
                margin: 5px 0px;
              }
      
              .reason_select{
                font-size: 15px;
                padding: 2px;
                width: 35vw;
              }
            }
           
  
            .reason_selfInput {
              position: relative;
              display: block;
              margin: 10px auto;
              margin-bottom: 20px;
              font-size: 15px;
              padding: 10px;
              width: 35vw;
              resize: none;
            }
  
            .line{
              position: relative;
              // background-color: aqua;
              width: 45vw;
              height: 10px;
              border-bottom: 0.5px solid black;
  
              @media screen and (max-width: 1000px) {
                width: 67vw;
              }
              
              @media screen and (max-width: 510px) {
                width: 83vw;
              }
  
                    @media screen and (max-width: 280px) {
                width: 85vw;
              }
            }

