                    position: relative;
                    all: unset;
                    cursor: pointer;
                    padding: 2px 10px;
                    border: 0.5px solid black;
                    font-size: 13px;
                    background-color: rgb(0, 0, 0);
                    font-weight: 500;
                    transition: 0.3s ease;
                    color: white;
                    border-radius: 50px;
                    margin-bottom: 2px;
                    
                    &:hover {
                      color: rgb(0, 0, 0);
                      background-color: rgb(255, 255, 255);
                    }
            
